<template>
	<view class="container">
		<view class="login-container">
			<!-- 提示登录的图标 -->
			<u-icon name="weixin-circle-fill" color="#AFAFAF" size="130"></u-icon>
			<view class="content">
				<view>申请获取以下权限</view>
				<text>获得你的公开信息(昵称，头像等)</text>
			</view>
			<!-- 登录按钮 getUserProfile 接口好像已被弃用-->
			<button class="btn-login" @click="getUserProfile">微信登录</button>
			<u-overlay :show="mask" @click="mask = false">
				<view class="warp">
					<view class="rect">
						<span class="title-phone">申请绑定您的手机</span>
						<span class="title-phone-des">点击按钮获取手机号</span>
						<div class="tick-phone">
							<u-button
								type="success"
								shape="circle"
								size="large"
								text="一键绑定手机"
								open-type="getPhoneNumber"
								@getphonenumber="getPhoneNumber"
							></u-button>
						</div>
					</view>
				</view>
			</u-overlay>
		</view>
	</view>
</template>

<script>
import { mapGetters, mapMutations, mapState } from 'vuex';
import WXBizDataCrypt from '@/common/WXBizDataCrypt.js';

export default {
	data() {
		return {
			code: '',
			openId: '',
			unionid: '',
			session_key: '',
			tel_show: '',
			mask: false
		};
	},
	//页面每次打开时
	onLoad() {
		this.getCodeAndProve();
	},
	methods: {
		...mapMutations(['updateUserInfo', 'updateOpenId', 'updateUserTel', 'updateHasLogin']),

		//授权
		getUserProfile() {
			console.log('2.获取微信授权用户信息userinfo');
			uni.getUserProfile({
				desc: '获取用户信息',
				success: res => {
					console.log(res);
					let userInfo = JSON.stringify(res.userInfo);
					console.log(userInfo);
					uni.setStorageSync('userInfo', userInfo);
					this.updateUserInfo();
					this.mask = true;
				},
				fail: err => {
					uni.showToast({
						icon: 'none',
						title: '您拒绝提供信息,将无法正常使用小程序'
					});
				}
			});
		},

		//获取code和凭证（可前台用wx官方获取 也可接自己后台获取）
		getCodeAndProve() {
			console.log('1.获取微信授权用户信息openId');
			const p = new Promise(resolve => {
				uni.login({
					provider: 'weixin',

					success: res => {
						resolve(res.code);
					}
				});
			});
			p.then(code => {
				//请求后端或者微信官方
				uni.request({
					//正式上线必须用后台
					//url: 'https://fjrcsx.com:8080/wxapi/system/user/login/' + code,
					//测试时候可以用微信官方
					url: 'https://api.weixin.qq.com/sns/jscode2session',
					method: 'GET',
					data: {
						// appid: 'wx6e4298babc9eea31', //我的小程序的APPID
						// secret: 'ed853baf69963fb5ac5a948b76eebfc1', //我的小程序的secret,
						// appid: 'wxf87c79c3f130155e', //你的小程序的APPID
						// secret: 'aba31ecebed3e9ab0445d86b1dca5a35', //你的小程序的secret,

						appid: 'wxef1ae936fbca0664', //甲方的小程序的APPID
						secret: '0a3b4b9f1f03ea53a0e7918d418b7788', //甲方的小程序的secret,
						js_code: code, //wx.login 登录成功后的code
						grant_type: 'authorization_code'
					},
					success: cts => {
						// 换取成功后 暂存这些数据 留作后续操作
						this.openId = cts.data.openid; //openid 用户唯一标识
						this.unionid = cts.data.unionid; //unionid 开放平台唯一标识   当公众号和小程序同时登录过才会有
						this.session_key = cts.data.session_key; //session_key  会话密钥
						uni.setStorageSync('openId', this.openId);
						this.updateOpenId();
					}
				});
			});
		},

		//获取用户手机（只有已经认证的小程序才能获取手机号或者使用云函数获取加密数据解密）
		getPhoneNumber(e) {
			console.log('3.getphone');
			console.log(e);
			if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
				uni.showToast({
					icon: 'none',
					title: '您拒绝提供手机号,将无法正常使用小程序'
				});
			} else {
				// 用户同意提供手机号
				// let pc = new WXBizDataCrypt('wx6e4298babc9eea31', this.session_key);
				// let pc = new WXBizDataCrypt('wxf87c79c3f130155e', this.session_key);
				let pc = new WXBizDataCrypt('wxef1ae936fbca0664', this.session_key);
				let data = pc.decryptData(e.detail.encryptedData, e.detail.iv); //dat

				uni.setStorageSync('userTel', data.phoneNumber);
				this.updateUserTel();
				uni.setStorageSync('hasLogin', true);
				this.updateHasLogin();
			}
			// console.log('手机号加载完毕。。。');
			let pages = getCurrentPages();
			let beforePage = pages[pages.length - 2];
			// 登录成功以后返回并刷新上一页
			uni.navigateBack({
				success: function() {
					beforePage.onLoad();
				}
			});
		}
	}
};
</script>

<style lang="less">
.login-container {
	// 登录盒子的样式
	height: 65%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	overflow: hidden;
	.content {
		width: 70%;
		border-top: 1px solid #ccc;
		padding-top: 40rpx;
		margin-top: 50rpx;
	}
	.content text {
		display: block;
		color: #9d9d9d;
		margin-top: 40rpx;
	}
	// 登录按钮的样式
	.btn-login {
		width: 75%;
		border-radius: 200rpx;
		margin-top: 50rpx;
		margin-bottom: 30rpx;
		background-color: #59c525;
		color: #fff;
	}
}
.warp {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100%;
	.rect {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		// padding: 30rpx;
		width: 85%;
		height: 25%;
		background-color: #fff;
		border-radius: 20rpx;
		.title-phone {
			font-size: 40rpx;
			margin: 10rpx;
		}
		.title-phone-des {
			color: #9d9d9d;
			margin: 10rpx;
		}
		.tick-phone {
			width: 80%;
			margin: 10rpx;
		}
	}
}
</style>
